/* bike_store/frontend/src/pages/HomePage.css */
.home-page {
    min-height: 100vh;
    background-color: #f8f9fa;
}

.hero-section {
    background-color: var(--primary-color);
    color: var(--light-text);
    padding: 80px 20px;
    text-align: center;
}

.hero-section h1 {
    font-size: 2.5em;
    margin-bottom: 20px;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
}

.hero-text {
    max-width: 800px;
    margin: 0 auto;
    font-size: 1.2em;
    line-height: 1.6;
}

.content-section {
    max-width: 1200px;
    margin: 40px auto;
    padding: 0 20px;
}

.content-section h2 {
    color: var(--secondary-color);
    text-align: center;
    margin-bottom: 30px;
}