/* bike_store/frontend/src/components/Footer.css */
.footer {
    background-color: var(--secondary-color);
    color: var(--light-text);
    padding: 40px 0 20px;
    margin-top: auto;
}

.footer-container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 20px;
}

.footer-content {
    display: flex;
    justify-content: space-around;
    margin-bottom: 30px;
}

.footer-section {
    flex: 1;
    max-width: 300px;
}

.footer-section h3 {
    color: var(--accent-color);
    margin-bottom: 20px;
    font-size: 1.2em;
}

.footer-links {
    list-style: none;
    padding: 0;
}

.footer-links li {
    margin: 10px 0;
}

.footer-links a {
    color: var(--light-text);
    text-decoration: none;
    transition: color 0.3s;
}

.footer-links a:hover {
    color: var(--primary-color);
}

.footer-bottom {
    text-align: center;
    padding-top: 20px;
    border-top: 1px solid #34495e;
}

.footer-bottom p {
    color: #bdc3c7;
    font-size: 0.9em;
}